.core {
  background: #6dbf5c;
  color: #ef6759;
  width: 100vw;
  max-width: 400px;
  padding: 15px;
  box-sizing: border-box; }
  .core .display {
    width: 100%; }
    .core .display .display-item {
      display: inline-block;
      vertical-align: top;
      height: 100%; }
    .core .display .display-inner {
      display: inline-block;
      box-sizing: border-box;
      background: #5e455f;
      text-align: left;
      vertical-align: top;
      padding: 5px 10px;
      border-radius: 2px;
      transition: .3s ease; }
      .core .display .display-inner > .title {
        text-align: center;
        font-size: 12px;
        color: #fffbe8; }
      .core .display .display-inner .info {
        width: 100px;
        display: inline-block;
        font-size: 12px;
        color: #fffbe8; }
        .core .display .display-inner .info .title {
          display: inline-block;
          font-size: 12px;
          margin-right: 5px; }
        .core .display .display-inner .info .sub {
          display: inline-block;
          font-size: 12px;
          opacity: .7; }
          .core .display .display-inner .info .sub span {
            margin-right: 3px; }
      .core .display .display-inner .view {
        background: #2f242f;
        display: inline-block; }
        .core .display .display-inner .view .info {
          position: absolute;
          left: 50%;
          top: 45%;
          transform: translate(-50%, -50%);
          font-size: 18px;
          width: 100%; }
          .core .display .display-inner .view .info.countDown {
            font-size: 100px;
            font-weight: bold;
            display: none; }
            .core .display .display-inner .view .info.countDown.act {
              display: inline-block; }
      .core .display .display-inner canvas {
        vertical-align: bottom;
        box-sizing: border-box;
        display: none; }
    .core .display .display-main {
      width: 65%;
      margin-right: 15px; }
      .core .display .display-main .display-inner {
        width: 100%;
        height: 100%;
        position: relative;
        text-align: center; }
        .core .display .display-main .display-inner .title {
          position: absolute;
          left: 50%;
          transform: translateX(-50%); }
        .core .display .display-main .display-inner .view {
          display: inline-block;
          height: calc(100% - 30px);
          width: 100%;
          overflow: hidden; }
          .core .display .display-main .display-inner .view .view-content {
            background: #2f242f; }
        .core .display .display-main .display-inner #no-webcam {
          display: none; }
        .core .display .display-main .display-inner video {
          height: 100%;
          width: auto;
          display: inline-block;
          margin: 0;
          padding: 0;
          vertical-align: bottom;
          transform: scaleX(-1);
          mix-blend-mode: lighten;
          opacity: .2;
          filter: blur(1px); }
    .core .display .display-sub {
      width: calc(35% - 15px); }
      .core .display .display-sub ul {
        position: relative;
        height: 100%; }
      .core .display .display-sub li {
        margin-bottom: 10px; }
        .core .display .display-sub li .display-inner {
          width: 100%;
          padding: 15px 5px;
          box-sizing: border-box; }
        .core .display .display-sub li .view {
          margin-right: 15px; }
        .core .display .display-sub li .info {
          float: left;
          transform: scale(0.8);
          white-space: nowrap;
          vertical-align: bottom; }
        .core .display .display-sub li .status {
          display: inline-block;
          height: 25px;
          width: calc(100% - 100px);
          background: #2f242f;
          float: left;
          transition: transform 100ms linear;
          transform: scaleX(1);
          transform-origin: left; }
  .core .panel {
    margin-top: 15px;
    background: #fffbe8;
    padding: 15px;
    border-radius: 3px;
    color: #fffbe8;
    box-sizing: border-box;
    width: 100%;
    display: none;
    transition: .3s ease; }
    .core .panel .function-group {
      display: none; }
      .core .panel .function-group.act {
        display: block; }
    .core .panel.disable {
      touch-action: none;
      pointer-events: none;
      opacity: .8; }
    .core .panel .check-groups {
      margin-bottom: 15px; }
      .core .panel .check-groups .check {
        background: #d3baa8;
        padding: 0 10px;
        border-radius: 3px;
        margin-right: 10px; }
        .core .panel .check-groups .check:last-child {
          margin-right: 0; }
        .core .panel .check-groups .check label {
          font-size: 12px;
          margin-left: 5px; }
    .core .panel .panel-groups .button {
      cursor: pointer;
      display: inline-block;
      background: #5e455f;
      margin-right: 10px;
      margin-bottom: 10px;
      padding: 5px 0px;
      border-radius: 3px;
      font-size: 12px;
      text-align: center;
      color: #f1f1f1cc;
      width: calc(50% - 5px); }
      .core .panel .panel-groups .button span.control {
        display: inline-block;
        height: 100%;
        width: 100%; }
      .core .panel .panel-groups .button input {
        margin-left: 5px;
        vertical-align: inherit; }
      .core .panel .panel-groups .button:active {
        background: #2f242f; }
      .core .panel .panel-groups .button:nth-child(even) {
        margin-right: 0; }
    .core .panel .panel-button {
      box-sizing: border-box;
      cursor: pointer;
      display: inline-block;
      background: #ef6759;
      padding: 15px 0;
      width: 100%;
      margin-top: 10px;
      text-align: center;
      border-radius: 3px; }
      .core .panel .panel-button.hidden {
        display: none; }
      .core .panel .panel-button.disable {
        display: none; }
      .core .panel .panel-button span {
        display: none; }
        .core .panel .panel-button span:first-child {
          display: inline-block; }
      .core .panel .panel-button.act {
        background: #d23c38; }
        .core .panel .panel-button.act span:first-child {
          display: none; }
        .core .panel .panel-button.act span:last-child {
          display: inline-block; }
      .core .panel .panel-button:active {
        background: #d23c38; }

[data-active="up"] #display_up, [data-active="left"] #display_left, [data-active="right"] #display_right, [data-active="down"] #display_down {
  background: #d23c38; }
